@mixin h1() {
    color: #0062FF;
    text-align: center;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-family: "Consolas";
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    position: relative;
}
@mixin h2() {
    color: #000;
    font-family: "Consolas";
    font-size: 28px;
    line-height: 36px;
    font-weight: 400;
}

